<template>
  <div  class='box'>
    <!--<input type="text" value='搜索歌曲、歌手'>-->
    <!--<h2>热门搜索</h2>-->
    <!--<span v-for="(item,index) in hotKey">-->
    <!--{{item.k}}-->
    <!--</span>-->

      <input type="text" value='搜索歌曲、歌手'>
      <h2>热门搜索</h2>
      <span v-for="(item,index) in hotKey">
             {{item.k}}
             </span>

    <router-view></router-view>
  </div>
</template>

<script>

  import {getHotKey} from 'Common/js/search'
  export default {
    name: "Search",
    data(){
      return{
        hotKey:[]
      }
    },
    methods:{
      _getHotKey() {
        getHotKey().then((res) => {
          this.hotKey = res.data.hotkey.slice(0, 10)

        })
      }

    },
    created(){
      this._getHotKey()

    }
  }
</script>

<style scoped lang="less">
  @import "~style/index.less";
  .box{
    background:@bg-color;
    color:@nav-color;
    position:absolute;
    .top(88);
    .bottom(0);
    input{
      .w(371);
      .h(40);
      color:@nav-color;
      background:@rank-color;
      border:none;
      border-radius:5px;
      .margin(10,0,10,15);
      text-indent:20px;
    }
    h2{
      font-size:14px;
      .margin(20,0,10,15);
    }
    span{
      font-size:14px;
      display:inline-block;
      .padding(5,10,5,10);
      background:#333;
      .margin(0,10,0,10);
      border-radius:5px;
      text-align:center;
      .l_h(20)
    }
  }
</style>



