<template>
  <div class='singer warpper'>
    <div class='scorllList content'>
      <ul class="group">
        <li
          v-for='(group,index) in list'
          :key="index"
          class="group-item"
        >
          <span class='title' :ref="group.title">{{group.title}}</span>
          <ul class='singer-item'>
            <li
              v-for='(item,index) in group.items'
              :key='index'
              @click="goSingerDetail(item)"
            >
               <img v-lazy="item.avator" width="50" height="50" alt="" class="avator">
              <p>
                {{item.name}}
              </p>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <div class='slider'>
      <ul @touchstart='touchBegin'
          @touchmove='touchMove'
      >
        <li v-for="(item,index) of sliderData"
            :key="index"
            :class="index===indexs?'checkColor':'normalColor'"
        >
        {{item}}
        </li>
      </ul>
    </div>
        <router-view></router-view>
  </div>
</template>
<script>
  import Data from 'data/singer.js';
  import BScroll from 'better-scroll';
  export default {
    data() {
      return {
        list: [],
        indexs:''
      }
    },
    //计算属性
    computed:{
      sliderData(){
        return this.list.map((item)=>{
          if(item.title==="热门"){
              return'热'
          }else{
              return item.title
          }
        })
      }
    },
    methods: {
      goSingerDetail(item){
        console.log(11)
        console.log(item)
        this.$router.push({name:'detail',params:item})
      },
      touchMove(e){
        // console.log('move')
        let moveY=e.touches[0].clientY-this.touchStartY
        let index=parseInt(moveY/17)+this.touchindex
        this.indexs=index
        //总的移动下标=初始下标+移动下标
        //abcdef
        //通过下标获取 title 获取绑定的元素值
        // console.log(index)

        let targe=this.sliderData[index]==='热'?'热门':this.sliderData[index]
        // console.log(targe)
        let element = this.$refs[targe][0]
        // console.log(element)
        this.scroll.scrollToElement(element)
        // let ele=document.querySelector(element)
        // ele.color=@font-color-header
        //console.log(this.sliderData[index])
      },
      touchBegin(e){
        // console.log('click')
        //获取点击的开始坐标
        this.touchStartY=e.touches[0].clientY
        //获取点击的字母在数组里的下标
        this.touchindex=this.sliderData.indexOf(e.target.innerText)
        this.indexs=this.touchindex
        let targe =e.target.innerText==='热'?'热门':e.target.innerText
        let element = this.$refs[targe][0]
        console.log(element.innerHTML)
        this.scroll.scrollToElement(element)
        // console.log(e)
      },

      //将数据变成我们可用的样子
      normalData(data) {
        // console.log(data)
        let lists = {
          'hot': {
            title: '热门',
            items: []
          },
          'A': {
            title: 'A',
            items: []
          }
        }
        for (let index = 0; index < data.length; index++) {
          //热门
          let img = `https://y.gtimg.cn/music/photo_new/T001R300x300M000${data[index].Fsinger_mid}.jpg?max_age=2592000`
          if (index <= 10) {
            lists.hot.items.push({
              name: data[index].Fsinger_name,
              id: data[index].Fsinger_id,
              mid:data[index].Fsinger_mid,
              avator: img,
            })
          }
          //按照首字母(Findex)分组
          if (!lists[data[index].Findex]) {
            //如果Findex不存在，创建新的分组
            lists[data[index].Findex] = {
              title: data[index].Findex,
              items: [{
                name: data[index].Fsinger_name,
                id: data[index].Fsinger_id,
                mid:data[index].Fsinger_mid,
                avator: img,
              }]
            }
          }else{//Finde已经存在，将其添加至分组
            lists[data[index].Findex].items.push({
              name: data[index].Fsinger_name,
              id: data[index].Fsinger_id,
              mid:data[index].Fsinger_mid,
              avator: img,
            })
          }
        }
        //提出不需要的项 按照首字母排序
        // 先进行拆分  ->排序 ->合并
        let hot=[];
        let itemArr=[];
        for(const key in lists){
          if(key==='hot'){
            hot.push(lists[key])
          }else{
            if(key.match(/[a-zA-Z]/)){
              itemArr.push(lists[key])
            }
          }
        }
        //按照title排序
        itemArr.sort((a,b)=>{
          return a.title.charCodeAt(0)-b.title.charCodeAt(0)
        })
        this.list=hot.concat(itemArr);
        // console.log(this.list)

      }
    },
    // name: "singer",
    created() {
      setTimeout(() => {
        // console.log(Data)
        let data=Data.data.list;
        this.normalData(data);
        //数据更新完的下一帧刷新执行
       /* this.$nextTick(()=>{
          new BScroll('.warpper')
        })*/
       setTimeout(()=>{
         this.scroll=new BScroll('.warpper',{click:true})
       },20)
        //浏览器1000毫秒/60次  刷新 16.66666ms
      }, 500)
    }
  }
</script>

<style scoped lang="less">
@import "~style/index.less";
.singer{
  //.bug(red);
  .w(375);
  position: fixed;
  .top(88);
  .bottom(0);
  overflow: hidden;
  .scorllList{
    ul{
      list-style: none;
      font-size: 0;
      .group-item{
        .margin(0,0,20,0);
      }
      .title{
        .w(375);
        .h(30);
        .l_h(30);
        .padding(0,0,0,20);
        font-weight: normal;
        font-size: @font-size-s;
        background: #333;
        color: @font-color-tab;
        display: block;
      }
      .singer-item{
        li{
          .h(70);
          /*.l_h(50);*/
          .w(375);
          display: flex;
          font-size: @font-size-m;
          .padding(20,0,0,30);
          color:  @font-color-tab;
          box-sizing: border-box;
          p{
            .h(50);
            .l_h(50);
            color:@font-color-tab;
            .padding(0,0,0,20);
          }
          .avator{
            border-radius: 50%;
          }
        }
      }
    }
  }
  //侧边栏样式
  .slider{
    ul{
      list-style: none;
      .normalColor{
        color:@font-color-tab;
      }
      .checkColor{
        color:@font-color-header;
      }
    }
    position:absolute;
    top:50%;
    right: 0;
    font-size: @font-size-s;
    transform: translateY(-50%);
    background: rgba(0,0,0,.3);
    text-align: center;
    width: 20px;
    border-radius: 10px;
    .padding(20,0,20,0)

  }
}
</style>



