<template>
  <div class="box">
  <div class='header'>
    <span @click='back' class="back"> < </span>
    <span>{{params.name}} </span>
  </div>
  <div class='img'>
    <img :src="params.avator" alt="">
  </div>
  <div class='singerDetail warpper' ref="singerDetail">
    <div class='singer-detail content'>
        <!--这里是歌手详情页-->

        <div class='songlist'>
          <ul>
            <li v-for='(item,index) in songs' :key='index'
                @click='playMusic(index)'
            >
              <div class="sname"> {{item.songname}}</div>
              <div class="aname">{{params.name}}-{{item.albumname}}</div>

            </li>
          </ul>
      </div>
    </div>
  </div>
  </div>
</template>
<script>
  import jsonp from 'Common/js/jsonpx.js'
  import BScroll from 'better-scroll'
  import {mapMutations} from 'vuex'
  export default {
    /* created(){
       console.log(this.$route)
     },*/
    name: 'detail',
    data() {
      return {
        songs: []
      }
    },
    methods: {
      // ...mapMutations({addSonglist:play.addSonglist}),
      playMusic(index) {
        this.$store.commit('addSongList', this.songs)
        this.$store.commit('changeIndex', index)
      },
      back() {
        this.$router.back();
      },
      normalSongList(list) {
        let arr = []
        list.forEach(element => {
          arr.push({
            albumname: element.musicData.albumname,
            name: element.musicData.singer[0].name,
            songname: element.musicData.songname,
            mid: element.musicData.singer[0].mid,
            songmid:element.musicData.songmid,
            albid:element.musicData.albummid,
            interval:element.musicData.interval

          })
        });
        console.log(arr)
        this.songs = arr
      },
      initSongData() {
        let url = `https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg?g_tk=1928093487&inCharset=utf-8&outCharset=utf-8&notice=0&format=jsonp&hostUin=0&needNewCode=0&platform=yqq&order=listen&begin=0&num=80&songstatus=1&singermid=${this.$route.params.mid}`
        jsonp(url)
          .then((data) => {
            this.normalSongList(data.data.list)
            console.log(data)
          })
      }
    },
    computed: {
      params() {
        return this.$route.params
      }
    },
    created() {
      // console.log(this.$route)
      // console.log(this.$store)
      if (!this.$route.params.name) {
        return this.$router.replace('/singer')
      }
      this.initSongData()

      // console.log(this.$route.params)
    },
    mounted() {
      let elobj=this.$refs.singerDetail;
      new BScroll(elobj,{click:true})
    }
  }
</script>
<style lang="less" scoped>
  @import '~style/index.less';
  .box{
    .w(375);
    .img{
      position: fixed;
      .top(0);
      .h(265);
      .w(375);
      overflow: hidden;
      img{
        width: 100%;
      }
    };
    .header {
      .back{
        position: absolute;
        .left(10);
        font-size: 32px;
        color: #ffcd32;
      }
      position: fixed;
      z-index: 5;
      top: 0;
      left: 0;
      width: 100%;
      .h(265);
      /*height: 100%;*/
      background: rgba(7,17,27,.4);

      color: #fff;
      font-size: @font-size-m;
      .l_h(44);
      text-align: center;
    }
    .singerDetail{
      .w(375);
      position: fixed;
      .top(265);
      .bottom(0);
      /*overflow: hidden;*/
      z-index: 8;
      .singer-detail {

        /*position: relative;*/
        background: @bg-color;

        .songlist{
          /*  position: absolute;
            .top(288);*/
          ul{
            li{
              /*display: flex;*/
              /*-ms-flex-align: center;*/
              /*align-items: center;*/
              /*box-sizing: border-box;*/
              .h(64);
              .sname{
                color:@font-color-item;
                font-size:@font-size-m;
                .padding(10,0,0,25)
              }
              .aname{
                color:@font-color-tab;
                font-size:@font-size-ms;
                .padding(0,0,10,25)
              }

            }
          }
        }
      }
    }
  }


</style>



