<template>
  <div class='rank warpper'>
    <div class='scorllList content'>
      <ul  class="group-item">
        <li v-for='(group,index) in list'
            :key="index"
            class="itembox">
          <!--{{item}}-->
          <img v-lazy="group.picUrl" width="100" height="100" alt="" class="pic">
          <ul  class="item">
            <li class="it" v-for="(item,index) in group.songList ">
              {{index+1}} {{item.songname}}-{{item.singername}}
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import Data from 'data/Rank.js';
  import BScroll from 'better-scroll';
  // console.log(Data)
  export default {
    name: "rank",
    data() {
      return {
        list: [],
        index: ''
      }
    },
    methods: {},
    created() {
      setTimeout(() => {
        // console.log(Data)
        let data = Data.data.topList;
        // this.normalData(data);
        this.list = data;
        //数据更新完的下一帧刷新执行
        /* this.$nextTick(()=>{
           new BScroll('.warpper')
         })*/
        setTimeout(() => {
          this.scroll = new BScroll('.warpper')
        }, 20)
        //浏览器1000毫秒/60次  刷新 16.66666ms
      }, 500)
    },
  }
</script>
<style scoped lang="less">
  @import "~style/index.less";

  .rank {
    .w(375);
    position: fixed;
    .top(88);
    .bottom(0);
    overflow: hidden;

    .itembox {
      display: flex;
      .margin(0, 20, 0, 20);
      .padding(20, 0, 0, 0);
      padding-top: 20px;
      height: 100px;
      /*background: #333;*/

      .item {
        line-height: 26px;
        list-style: none;
        flex: 1;
        display: -ms-flexbox;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 20px;
        height: 100px;
        overflow: hidden;
        background: #333;
        color: hsla(0, 0%, 100%, .3);
        font-size: 12px;
      }
    }
  }


</style>



