<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for='item in list'>
        <a :href="item.linkUrl">
          <img :src="item.picUrl" >
        </a>
      </div>
    </div>
    <!-- 如果需要分页器 -->
    <div class="swiper-pagination"></div>

    <!-- 如果需要滚动条 -->
    <div class="swiper-scrollbar"></div>
  </div>
</template>

<script>
  import Swiper from 'swiper'
  export default {
    name:'MBanner',
    data(){
      return{
        list:[]
      }
    },
    methods: {
      initBanner(){
        var mySwiper = new Swiper ('.swiper-container', {
          autoplay:true,
          loop: true ,// 循环模式选项
          // 分页器
          pagination: {
            el: '.swiper-pagination',
          },
          //滚动条
          scrollbar: {
            el: '.swiper-scrollbar',
          },
        })
      }
    },
    created(){
      //
      this.$axios.get('/banner/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg?_=1551769639556&g_tk=5381&uin=0&format=json&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1')
        .then((res)=>{
          console.log('获取banner')
          console.log(res)
          this.list=res.data.slider
          this.$nextTick(()=>{
            this.initBanner()
          })

        })
        .catch((err)=>{
          console.log(err)
        })
    },
    mounted() {
      // this.initBanner()
      //初始化dome的操作不能放到created
    }
  }
</script>

<style scoped lang="less">
  @import "~style/index.less";
  @import url('../../../../node_modules/swiper/dist/css/swiper.css');
  .swiper-slide{
    img{
      .w(375);
      .h(150);
    }
  }

</style>



