<template>
    

      <div class="wrapper" ref="wrapper">
        <div class="content">
          <div class="banner">
          <m-banner></m-banner>
          </div>
          <h2>热门歌单推荐</h2>
        <ul class="recomList">
          <li :key="index" v-for="(item,index) in list">
           <div class="icon">
             <img width="60" height="60" v-lazy="item.imgurl" alt="">
           </div>
            <div class="text">
              <p class="name">{{item.creator.name}}</p>
              <p class="dissName">{{item.dissname}}</p>
            </div>
          </li>
        </ul>
        <!-- 这里可以放一些其它的 DOM，但不会影响滚动 -->
      </div>
      </div>

</template>

<script>
  import MBanner from "common/m-banner/m-banner";
  import BScroll from "better-scroll";
    export default {
        name: "Recommend",
      components:{MBanner},
      data(){
          return({
            list:[],
          })
      },
      created(){
        let url="/hy/music/api/getDiscList?g_tk=1928093487&inCharset=utf-8&outCharset=utf-8&notice=0&format=json&platform=yqq&hostUin=0&sin=0&ein=29&sortId=5&needNewCode=0&categoryId=10000000&rnd=0.7421194253490724"
        this.$axios.get(url)
          .then((res)=>{
            this.list=res.data.list;
            console.log(res);
          })
      },
      mounted(){
          let elobj=this.$refs.wrapper;
        new BScroll(elobj)
      },
      destroyed() {
          console.log("销毁了");
      }
    }
</script>

<style scoped lang="less">
@import "~style/index.less";
  .wrapper{
    .banner{
      .w(375);
      .h(150);
      background: orange;
    }
    h2{
      font-weight:normal;
      text-align: center;
      font-size: @font-size-m;
      color: @font-color-header;
      .padding(20,0,20,0)
    }

    position: fixed;
    .top(88);
    .left(0);
    .right(0);
    .bottom(0);
   // .bug(red);
    overflow: hidden;
    .recomList{
          li{
            .padding(0,15,20,20);
            display: flex;
            font-size: @font-size-m;
            .icon{
              .padding(0,20,0,0)
            }
            .name{
              color: @font-color-item;
              .padding(10,0,5,0)
            };
            .dissName{
              font-size: @font-size-s;
              color:@font-color-tab;
            }
          }
    }
  }
</style>



