<template>

    <div class="player"  v-if="songList.length" ref="playerbox">
      <div class="back" @click="back">
        <a class="iconfont">&#xe679 </a>
      </div>

      <div v-show="show" class="normalScreen">
        <!--大的播放器-->
        <!--{{songList[nowIndex]}}-->
       <!-- {{currentSong}}-->
        <div class="bg">
          <img :src="currentSong.imgUrl" alt="">
        </div>
      <!--  歌名-->
        <div class="top">
          {{currentSong.songname}}
        </div>
      <!--  歌手名-->
        <p>{{currentSong.name}}</p>
      <!--  CD-->
        <div class="cd-wrapper">
          <div class="cd">
            <img :src="currentSong.imgUrl" alt="">
          </div>
        </div>
       <!-- 控制条-->

        <div class="control">
         <!-- controls canplay updatetime-->
          <audio :src="currentSong.songUrl" ref="audio" @timeupdate="update"></audio>

          <div class="lyricbox">
          <p v-html='lyric[nowLine].txt' class="lyric"></p>
            <!--<p>歌词：{{nowLine}}</p>-->
          </div>
          <hr>
          <div class="jindu">
           <!-- {{(nowdate/60).toFixed(2)}}
            &lt;!&ndash;<el-slider v-model="nowdate" :format-tooltip="formatTooltip" class="tiao" ></el-slider>&ndash;&gt;
            <el-progress :percentage="nowdate*100/currentSong.interval" color="#ffcd32"></el-progress>
            {{(currentSong.interval/60).toFixed(2)}}-->
            <mt-progress :value="nowdate*100/currentSong.interval" class="tiao" >
              <div slot="start">{{(nowdate/60).toFixed(2)}}</div>
              <div slot="end">{{(currentSong.interval/60).toFixed(2)}}</div>
            </mt-progress>
          <!--  <mt-range :value.sync="nowdate*100/currentSong.interval">
              <div slot="start">{{(nowdate/60).toFixed(2)}}</div>
              <div slot="end"> {{(currentSong.interval/60).toFixed(2)}}</div>
            </mt-range>-->
          </div>
          <div class="playControl">
            <!--<button @click="changeIndex('pre')" class="pre"> < </button>-->
            <div @click="changeIndex('pre')" class="pre"><a class="iconfont ">&#xe679</a></div>
            <div @click="playMusic" class="play">
              <a class="iconfont " v-show="!playshow">&#xe74f</a>
              <a class="iconfont " v-show="playshow">&#xe750</a>
            </div>
            <!--<button @click="playMusic" class="play">play/pause</button>-->
            <!--<button @click="changeIndex('next')" class="next"> > </button>-->
            <div @click="changeIndex('next')" class="next"><a class="iconfont ">&#xe6a3</a></div>
           </div>
        </div>
      </div>
      <div v-show="!show" class="miniPlayer" ref="miniPlayer">
        小的播放器
      </div>
    </div>
</template>

<script>
  import{mapState,mapGetters,mapMutations}from 'vuex'
  import { Base64 } from 'js-base64';
  import Lyric from 'lyric-parser'
  import { Range } from 'mint-ui';

  // Vue.component(Range.name, Range);
    export default {
      name: "player",
      computed:{
        ...mapGetters(['currentSong']),
        ...mapState({
          songList:state=>state.play.songList,
          nowIndex:state=>state.play.nowIndex,
        })
      },
      data(){
        return{
          playshow:false,
          show:true,
          nowdate:0,
          nowLine:0,
          lyric:[{txt:'...'}]
          // lyric:[]
        }
      },
      watch:{
        currentSong(newSong,oldSong){
          //监听歌曲的变化，重新请求歌词
          let url =`/hy/music/api/lyric?g_tk=1928093487&inCharset=utf-8&outCharset=utf-8&notice=0&format=json&songmid=${newSong.songmid}&platform=yqq&hostUin=0&needNewCode=0&categoryId=10000000&pcachetime=1552266108016`
          this.$axios.get(url)
            .then((data)=>{
              // 将歌词从base64转化为utf8格式
              let text=Base64.decode(data.lyric)
              // console.log(data)
              //通过lyric-parser 插件解析歌词 https://github.com/ustbhuangyi/lyric-parser
              this.lyricobj =new Lyric(text,(data)=>{
                // console.log('歌词播放')
                  this.nowLine=data.lineNum

              })
              this.lyric=this.lyricobj.lines
                // console.log(this.lyricobj.lines[this.nowLine].txt)
                this.playMusic();
                this.playshow=true;
            })
        }
      },

      methods:{
        //时间进度
       /* formatTooltip(val) {
          return val /this.currentSong.interval ;
        },*/
        back(){
          let playerbox=this.$refs.playerbox
          let miniPlayer=this.$refs.miniPlayer
          this.show=!this.show

        },
        update(e){
          // console.log(e)
          this.nowdate=e.srcElement.currentTime
          if(this.currentSong.interval<=e.srcElement.currentTime){
            this.changeIndex('next')
          }
        },
        ...mapMutations(['changeIndex']),
        playMusic(){
          let audio=this.$refs.audio
          // console.log(audio)
          this.playshow=!this.playshow
          if(audio.paused){
            audio.play()
            // this.lyricobj.play()
            //自动判断是播放还是暂停
            this.lyricobj.togglePlay()
          }else{
            audio.pause()
            this.lyricobj.togglePlay()
          }
        }
      },

      created() {
        console.log(this.$store )
      }
    }
</script>

<style scoped lang="less">
  @import "../../../assets/iconfont/iconfont.css";
  @import "~style/index.less";
  .player{
    .miniPlayer{

    }
    .w(375);
    position: fixed;
    top: 0;
    bottom: 0;
    background: @bg-color;
    color: #fff;
    text-align: center;
    .back{
      position:fixed;
      .top(0);
      .left(20);
      font-size: @font-size-xl;
      color: @font-color-header;

    }
    .bg{
      position: fixed;
      .w(375);
      top: 0;
      bottom: 0;
      z-index: -3;
      img{
        width: 100%;
        height: 100%;
        opacity: 0.6;
        //高斯模糊
        filter:blur(25px);
      }
    }
    .top{
      .w(375);
      .h(40);
      font-size: @font-size-l;
      .l_h(40);
    }
    p{
      font-size: @font-size-s;
      .h(20);
      .l_h(20);
    }
    .control{
      .lyricbox{
        /*.w(30);*/
     /*   .bug(red);*/
        .h(30);
        .padding(10,0,10,0);
        .lyric{
          font-size:@font-size-m;
        }
      }
      .jindu{

        font-size: @font-size-s;
        .w(320);
        margin:0 auto;
        /*.margin(30,0,0,0);*/
        .tiao{
          .padding(30,0,0,0);
          div{
            .padding(0,10,0,10);
          }
        }
      }
      .playControl{
        position: fixed;
        .bottom(0);
        margin: 0 auto;
        /*color:@font-color-header;*/
       /* .bug(red);*/
        display: flex;
        justify-content: center;
        align-items: center;
        .w(375);
        .h(150);
        .pre{
          .w(70);
          .h(70);
          border-radius: 50%;
          /*.bug(red)*/

        }
        .play{
          .w(70);
          .h(70);
          border-radius: 50%;
          /*.bug(red)*/
        }

        .next{
          .w(70);
          .h(70);
          border-radius: 100%;
          /*.bug(red)*/
        }

      }
    }
    .cd-wrapper{
      .w(375);
      .h(375);
    /*  .bug(red);*/
      //弹性盒居中
      display: flex;
      justify-content: center;
      align-items: center;
      .cd{
        /*margin: 0 auto;*/
        /*transition:all 1s linear;*/
        @-webkit-keyframes gogogo {
          0%{
            -webkit-transform: rotate(0deg);
            /*border:5px solid red;*/
          }
          50%{
            -webkit-transform: rotate(180deg);
          /*  background:black;*/
            /*border:5px solid yellow;*/
          }
          100%{
            -webkit-transform: rotate(360deg);
            /*background:white;
            border:5px solid red;*/
          }
        }
        -webkit-animation:gogogo 20s infinite linear ;
        width:85%;
        height:85%;
        border-radius: 50%;
        border: 10px solid rgba(0,0,0,.1);
        img{
          width:100%;
          height:100%;
          border-radius: 50%;

        }
      }
    }
  }

</style>



