import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)
//组件引入
import Recommend from 'pages/Recommend/Recommend'
import Singer from 'pages/Singer/Singer'
import Rank from 'pages/Rank/Rank'
import Search from 'pages/Search/Search'
import SingerDetail from "pages/SingerDetail/SingerDetail";

let router = new Router({
  routes: [
    {
      path: '/recommend',
      name: 'Recommend',
      component: Recommend
    },
    {
      path: '/singer',
      name: 'Singer',
      component: Singer,
      children:[{
        path:'detail',
        name:'detail',
        component:SingerDetail
      }]
    },
    {
      path: '/rank',
      name: 'Rank',
      component: Rank
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/',
     redirect:'/recommend'
    },

  ]
})
router.beforeEach((to,from,next)=>{
  // console.log(to)
  // console.log(from)
  let login=true;
  if(to.name==='Rank'){
    if(login){
      next()
    }else{
      next(false)
    }
  }else{
    next()
  }
})
export default router;



