import state from './state'
export  default {
  addSongList(state,params){
    state.songList=params
  },
  changeIndex(state,params){

    if(params==="next"&&state.nowIndex< state.songList.length){
      state.nowIndex++
    }else if(params==="pre"&&state.nowIndex>0){
      state.nowIndex--
    }else if(typeof(params)==='number'){
      state.nowIndex=params;
    }else{
      return false
    }
  },
}
