export default{
  currentSong(state){
    if(state.songList.length===0){
      return{};
    }
    let albid = state.songList[state.nowIndex].albid
    let songmid=state.songList[state.nowIndex].songmid
    let imgUrl=`https://y.gtimg.cn/music/photo_new/T002R300x300M000${albid}.jpg?max_age=2592000`
    let song = state.songList[state.nowIndex]
    song.imgUrl=imgUrl
                //  http://dl.stream.qqmusic.qq.com/http://dl.stream.qqmusic.qq.com/C4000016j8j527uqHq.m4a?guid=485611810&vkey=5C792146967EB47D91DC6D9CAB1757960618DE105B57B241D65EB1735DEF5387F8960E3216273029F85C39030065A73D2C71C8C1888EEBC2&uin=0&fromtag=38
    song.songUrl=`http://dl.stream.qqmusic.qq.com/http://dl.stream.qqmusic.qq.com/C400${ songmid}.m4a?guid=485611810&vkey=5C792146967EB47D91DC6D9CAB1757960618DE105B57B241D65EB1735DEF5387F8960E3216273029F85C39030065A73D2C71C8C1888EEBC2&uin=0&fromtag=38
`
    return state.songList[state.nowIndex]
  }
}
